.em-chat-content-item-footbar {
    margin-top: 8px;
    padding-top: 8px;
    padding-left: 50px;
    .add-btn {
        display: none;
        color: rgb(88, 96, 105);
        &.none {
            position: absolute;
            bottom: 0;
        }
    }
    .add-emoji-btn {
        &.none {
            left: 8px;
        }
    }
    .add-tag-btn {
        &.none {
            left: 45px;
        }
    }
    > .ui.label {
        cursor: pointer;
    }
    .emoji-item {
        margin-right: 8px;
        img {
            cursor: pointer;
        }
        &:last-child {
            margin-right: 0;
        }
    }
    .ui.popup.tags {
        > .content {
            width: 265px;
            > .ui.input {
                width: 20px;
            }
        }
        &.customTag {
            > .content {
                > .ui.label {
                    display: none;
                }
                > .ui.input {
                    width: 265px;
                }
            }
        }
    }
}
